.c-btn {
  display: inline-block;
  text-align: center;
  position: relative;
  transition: 0.3s;
  z-index: 1;
}

.c-btn::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transform-origin: 0% 50%;
  transform: scaleX(0);
  transition: transform ease 0.3s;
}

.c-btn:hover {
  opacity: unset;
}

.c-btn:hover::before {
  transform-origin: 0% 50%;
  transform: scaleX(1);
}

.c-btn--background-black {
  max-width: 20.9375rem;
  width: 100%;
  background-color: #333;
  border: solid 1px #fff;
  color: #fff;
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
  position: relative;
}

.c-btn--background-black::before {
  background: #fff;
}

.c-btn--background-black::before {
  opacity: unset;
}

.c-btn--background-black:hover {
  color: #333;
}

.c-btn--background-white {
  max-width: 20.9375rem;
  width: 100%;
  background-color: #333;
  border: solid 1px #fff;
  color: #fff;
}
